import { ICard, IConnection, ArrowType, MindMapData } from '../types/CoreTypes';
import { LayoutAlgorithm, LayoutOptions, calculateNewCardPosition } from './layoutUtils';
import { generateCardId, generateUniqueCardId, generateUniqueConnectionId } from './idGenerator';
import { GraphLayoutEngine } from './layout/graphLayoutEngine';
import { getRandomColor } from './ui/colors';

// 
function simplifyContent(content: string): string {
  return content
    // 
    .replace(/\n/g, ' ')
    // 
    .replace(/["']/g, '')
    // 
    .substring(0, 30)
    // 
    .concat(content.length > 30 ? '...' : '');
}

/**
 * 
 */
export const ExportImportUtils = {
  /**
   * Mermaid
   */
  exportToMermaid: (data: MindMapData): string => {
    let mermaidCode = 'graph LR\n';
    
    // 
    const processedConnections = new Set<string>();
    
    // ID
    const cardIds = new Map<string, string>();
    data.cards.forEach((card, index) => {
      // ID
      const simpleId = `node${index + 1}`;
      cardIds.set(card.id, simpleId);
    });
    
    // 
    data.connections.forEach(conn => {
      const startCardId = cardIds.get(conn.startCardId);
      const endCardId = cardIds.get(conn.endCardId);
      
      if (startCardId && endCardId) {
        const startCard = data.cards.find(c => c.id === conn.startCardId);
        const endCard = data.cards.find(c => c.id === conn.endCardId);
        
        if (startCard && endCard) {
          // 
          const startContent = simplifyContent(startCard.content);
          const endContent = simplifyContent(endCard.content);
          
          // Mermaid
          let connectionSymbol = '-->';
          if (conn.arrowType) {
            switch (conn.arrowType) {
              case ArrowType.NONE:
                connectionSymbol = '---';
                break;
              case ArrowType.START:
                connectionSymbol = '<--';
                break;
              case ArrowType.END:
                connectionSymbol = '-->';
                break;
              case ArrowType.BOTH:
                connectionSymbol = '<-->';
                break;
            }
          }
          
          // 
          let connectionText = `  ${startCardId}[${startContent}] ${connectionSymbol} `;
          
          // 
          if (conn.label && conn.label.trim()) {
            // 
            const simpleLabel = simplifyContent(conn.label);
            connectionText += `|${simpleLabel}| `;
          }
          
          connectionText += `${endCardId}[${endContent}]`;
          
          mermaidCode += `${connectionText}\n`;
          
          // 
          processedConnections.add(conn.startCardId);
          processedConnections.add(conn.endCardId);
        }
      }
    });
    
    // 
    data.cards.forEach(card => {
      if (!processedConnections.has(card.id)) {
        const cardId = cardIds.get(card.id);
        if (cardId) {
          const content = simplifyContent(card.content);
          mermaidCode += `  ${cardId}[${content}]\n`;
        }
      }
    });
    
    return mermaidCode;
  },
  
  /**
   * Mermaid
   */
  importFromMermaid: (mermaidCode: string): MindMapData => {
    try {
      const lines = mermaidCode.split('\n');
      const cards: ICard[] = [];
      const connections: IConnection[] = [];
      
      // IDID
      const nodeToCardId = new Map<string, string>();
      
      // 
      for (let line of lines) {
        const trimmedLine = line.trim();
        
        // 
        if (!trimmedLine || trimmedLine.startsWith('graph') || trimmedLine.startsWith('flowchart')) {
          continue;
        }
        
        //  - 
        let connectionMatch = null;
        let startId = '', startContent = '', connectionSymbol = '', endId = '', endContent = '';

        // 
        // : 1  2 A[]  A 
        // 
        connectionMatch = trimmedLine.match(/([A-Za-z0-9_\u4e00-\u9fa5]+)(?:\[([^\]]*)\])?\s*(-->|<--|<-->|---)\s*([A-Za-z0-9_\u4e00-\u9fa5]+)(?:\[([^\]]*)\])?/);

        if (connectionMatch) {
          startId = connectionMatch[1];
          startContent = connectionMatch[2] || startId; // ID
          connectionSymbol = connectionMatch[3];
          endId = connectionMatch[4];
          endContent = connectionMatch[5] || endId; // ID
        }

        if (connectionMatch) {
          // 
          const cleanStartContent = startContent.replace(/^["']|["']$/g, '');
          const cleanEndContent = endContent.replace(/^["']|["']$/g, '');

          // 
          const startCardId = getOrCreateCard({ id: startId, content: cleanStartContent }, nodeToCardId, cards);
          const endCardId = getOrCreateCard({ id: endId, content: cleanEndContent }, nodeToCardId, cards);

          // 
          let arrowType = ArrowType.END; // 

          // 
          if (connectionSymbol === '<-->') {
            arrowType = ArrowType.BOTH;
          } else if (connectionSymbol === '<--') {
            arrowType = ArrowType.START;
          } else if (connectionSymbol === '-->') {
            arrowType = ArrowType.END;
          } else if (connectionSymbol === '---') {
            arrowType = ArrowType.NONE;
          }

          // 
          const label = extractConnectionLabel(trimmedLine) || '';
          connections.push({
            id: generateUniqueConnectionId(),
            startCardId,
            endCardId,
            label,
            arrowType
          });
        } else {
          // 
          const nodeMatch = trimmedLine.match(/^([A-Za-z0-9_\u4e00-\u9fa5]+)\[([^\]]*)\]$/);
          if (nodeMatch) {
            const nodeId = nodeMatch[1];
            const nodeContent = nodeMatch[2].replace(/^["']|["']$/g, ''); // 

            // 
            getOrCreateCard({ id: nodeId, content: nodeContent }, nodeToCardId, cards);
          }
        }
      }
      
      // 
      if (cards.length === 0) {
        cards.push({
          id: generateCardId(),
          content: 'Mermaid',
          x: 100,
          y: 100,
          width: 200,
          height: 100,
          color: '#ffcccc'
        });
      }
      
      // 
      const layoutEngine = new GraphLayoutEngine();
      layoutEngine.arrangeCards(cards, connections);

      return { cards, connections };
    } catch (error) {
      console.error('Mermaid:', error);
      
      // 
      return {
        cards: [{
          id: generateCardId(),
          content: `: ${error instanceof Error ? error.message : ''}`,
          x: 100,
          y: 100,
          width: 200,
          height: 100,
          color: '#ffcccc'
        }],
        connections: []
      };
    }
  },

  /**
   * PNG
   * URL
   */
  exportToPNG: async (
    _data: MindMapData, // 
    canvasRef: React.RefObject<HTMLDivElement>,
    options?: {
      scale?: number,
      backgroundColor?: string,
      format?: 'png' | 'jpeg' | 'webp',
      autoDownload?: boolean
    }
  ): Promise<string | null> => {
    if (!canvasRef.current) {
      console.error("");
      return null;
    }

    const scale = options?.scale || 2;
    const format = options?.format || 'png';
    const backgroundColor = options?.backgroundColor || '#ffffff';
    const autoDownload = options?.autoDownload || false;
    const mimeType = `image/${format}`;

    try {
      // html2canvas
      const html2canvas = await import('html2canvas');
      const canvas = await html2canvas.default(canvasRef.current, {
        backgroundColor: backgroundColor,
        scale: scale, // 
        useCORS: true, // 
        logging: false, // 
        allowTaint: true, // 
        foreignObjectRendering: true // ForeignObject
      });

      // URL
      const dataUrl = canvas.toDataURL(mimeType, format === 'jpeg' ? 0.9 : undefined);

      // 
      if (autoDownload) {
        const link = document.createElement('a');
        const timestamp = new Date().toISOString().slice(0, 19).replace(/[T:]/g, '-');
        link.download = `kbstorm-${timestamp}.${format}`;
        link.href = dataUrl;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        return "success";
      }

      // URI
      return dataUrl;
    } catch (error) {
      console.error(`${format.toUpperCase()}:`, error);
      return null;
    }
  },

  /**
   * Markdown
   * "---"
   */
  exportToMarkdown: (data: MindMapData): string => {
    // 
    let mdContent = '';
    
    //  - "kbstorm"
    mdContent += '# kbstorm\n\n';
    
    // ""0
    const inDegrees = new Map<string, number>();
    data.cards.forEach(card => inDegrees.set(card.id, 0));
    
    // 
    data.connections.forEach(conn => {
      const endCard = conn.endCardId;
      inDegrees.set(endCard, (inDegrees.get(endCard) || 0) + 1);
    });
    
    // 0
    const rootCardIds = Array.from(inDegrees.entries())
      .filter(([_, degree]) => degree === 0)
      .map(([id, _]) => id);
    
    // 
    const startCards = rootCardIds.length > 0 
      ? data.cards.filter(card => rootCardIds.includes(card.id)) 
      : data.cards;
    
    // ID
    const processedCardIds = new Set<string>();
    
    // 
    const processCard = (card: ICard, isFirst: boolean = true): void => {
      if (processedCardIds.has(card.id)) return;
      
      processedCardIds.add(card.id);
      
      // 
      if (!isFirst) {
        mdContent += '---\n\n';
      }
      
      // 
      mdContent += `${card.content.trim()}\n\n`;
      
      // 
      const connectedCards = data.connections
        .filter(conn => conn.startCardId === card.id)
        .map(conn => data.cards.find(c => c.id === conn.endCardId))
        .filter((c): c is ICard => c !== undefined);
      
      // 
      connectedCards.forEach((connCard) => {
        processCard(connCard, false);
      });
    };
    
    // 
    let isFirst = true;
    startCards.forEach(card => {
      processCard(card, isFirst);
      isFirst = false;
    });
    
    // 
    data.cards
      .filter(card => !processedCardIds.has(card.id))
      .forEach(card => {
        // 
        if (!isFirst) {
          mdContent += '---\n\n';
        }
        mdContent += `${card.content.trim()}\n\n`;
        isFirst = false;
      });
    
    // 
    const minimalCardData = data.cards.map(card => ({
      id: card.id,
      x: Math.round(card.x),
      y: Math.round(card.y),
      width: card.width,
      height: card.height,
      color: card.color
    }));
    
    // 
    const minimalConnectionData = data.connections.map(conn => ({
      id: conn.id,
      from: conn.startCardId,
      to: conn.endCardId,
      ...(conn.label ? { label: conn.label } : {}),
      ...(conn.arrowType !== undefined ? { arrowType: conn.arrowType } : {}) // 
    }));
    
    // 
    const metadata = {
      version: "1.0",
      cards: minimalCardData,
      connections: minimalConnectionData
    };

    // MarkdownspanCSS
    mdContent += `\n\n<span style="display:none"><!-- mindmap-metadata
${JSON.stringify(metadata, null, 0)}
mindmap-metadata --></span>`;
    
    return mdContent;
  },
  
  /**
   * Markdown
   * 
   * 1. Markdown - 
   * 2. Markdown - 
   */
  importFromMarkdown: (
    mdContent: string, 
    layoutInfo?: {
      algorithm: LayoutAlgorithm,
      options: LayoutOptions,
      viewportInfo?: {
        viewportWidth: number,
        viewportHeight: number,
        zoom: number,
        pan: { x: number, y: number }
      }
    },
    cardDefaults?: {
      defaultColor?: string,
      defaultWidth?: number,
      defaultHeight?: number,
      defaultPadding?: number
    }
  ): MindMapData | null => {
    try {
      // 
      const defaults = {
        color: cardDefaults?.defaultColor || getRandomColor(),  // 
        width: cardDefaults?.defaultWidth || 200,        // 
        height: cardDefaults?.defaultHeight || 100,      // 
        padding: cardDefaults?.defaultPadding || 10      // 
      };

      // 
      const useRandomColors = cardDefaults?.defaultColor === "random" || !cardDefaults?.defaultColor;

      // HTMLspan
      let cleanContent = mdContent.replace(/<span[^>]*>[\s\S]*?<\/span>/g, '')
        .replace(/<!--[\s\S]*?-->/g, '')
        .trim();
      
      // 
      const metadataMatch = mdContent.match(/<!-- mindmap-metadata\n([\s\S]*?)\nmindmap-metadata -->/);
      
      // 
      if (metadataMatch && metadataMatch[1]) {
        // JSON - 
        const metadata = JSON.parse(metadataMatch[1]);
        
        if (!metadata.cards || !metadata.connections) {
          console.error('');
          return null;
        }
        
        // 
        const cards: ICard[] = metadata.cards.map((card: any) => ({
          id: card.id,
          content: '', // Markdown
          x: card.x,
          y: card.y,
          width: card.width || defaults.width,
          height: card.height || defaults.height,
          color: card.color || defaults.color
        }));
        
        // 
        cleanContent = cleanContent.replace(/<span[^>]*>[\s\S]*?<\/span>/g, '')
          .replace(/<!--[\s\S]*?-->/g, '')
          .trim();
        
        //  - 
        cleanContent = cleanContent.replace(/^# [^\n]*\n+/, '');
        
        // 
        let contentBlocks = cleanContent
          // 
          .replace(/\r\n/g, '\n')
          // 
          .replace(/\n\s*\n/g, '\n')
          // 
          .split(/\n\s*---\s*\n/)
          .map(block => block.trim())
          .filter(block => block.length > 0);
        
        // 
        let index = 0;
        while (index < contentBlocks.length && index < cards.length) {
          cards[index].content = contentBlocks[index];
          index++;
        }
        
        // 
        const connections: IConnection[] = metadata.connections.map((conn: any) => ({
          id: conn.id,
          startCardId: conn.from,
          endCardId: conn.to,
          label: conn.label || '',
          arrowType: conn.arrowType !== undefined ? conn.arrowType : ArrowType.END // END
        }));
        
        return { cards, connections };
      } 
      else {
        //  - 
        console.log('');
        
        // 
        cleanContent = cleanContent.replace(/^# [^\n]*\n+/, '');
        
        // "---"
        let contentBlocks = cleanContent
          // 
          .replace(/\r\n/g, '\n')
          // 
          .replace(/\n\s*\n/g, '\n')
          // 
          .split(/\n\s*---\s*\n/)
          .map(block => block.trim())
          .filter(block => block.length > 0);
        
        // 
        if (contentBlocks.length <= 1) {
          const paragraphs = cleanContent.split(/\n\n+/).map(p => p.trim()).filter(p => p.length > 0);
          if (paragraphs.length > 0) {
            contentBlocks = paragraphs;
          }
        }
        
        // 
        if (contentBlocks.length === 0 && cleanContent.trim()) {
          contentBlocks.push(cleanContent);
        }
        
        // 
        const cards: ICard[] = [];
        const connections: IConnection[] = []; // 
        
        // 
        const currentLayout = layoutInfo || { 
          algorithm: 'grid' as LayoutAlgorithm, 
          options: { spacing: 180, jitter: 10 } 
        };
        
        // 
        let lastPosition = { x: 100, y: 100 };
        
        // 
        contentBlocks.forEach((content) => {
          // 
          const cardId = generateUniqueCardId();

          // 
          const cardSize = { width: defaults.width, height: defaults.height };
          const position = calculateNewCardPosition(
            lastPosition,
            { width: defaults.width, height: defaults.height },
            cards,
            currentLayout.algorithm,
            currentLayout.options,
            currentLayout.viewportInfo,
            cardSize // 
          );
          
          // 
          lastPosition = position;
          
          // 
          cards.push({
            id: cardId,
            content: content,
            x: position.x,
            y: position.y,
            width: defaults.width,
            height: defaults.height,
            color: useRandomColors ? getRandomColor() : defaults.color
          });
        });
        
        return { cards, connections };
      }
    } catch (error) {
      console.error('Markdown:', error);
      return null;
    }
  },

};


// 
function getOrCreateCard(
  nodeInfo: { id: string, content: string },
  nodeToCardId: Map<string, string>,
  cards: ICard[]
): string {
  // 
  if (nodeToCardId.has(nodeInfo.id)) {
    const existingCardId = nodeToCardId.get(nodeInfo.id)!;
    // ID
    const existingCard = cards.find(card => card.id === existingCardId);
    if (existingCard && nodeInfo.content !== nodeInfo.id && existingCard.content === nodeInfo.id) {
      existingCard.content = nodeInfo.content;
    }
    return existingCardId;
  }

  // 
  const cardId = generateUniqueCardId();
  nodeToCardId.set(nodeInfo.id, cardId);

  cards.push({
    id: cardId,
    content: nodeInfo.content,
    x: 0,  // 
    y: 0,
    width: 160,
    height: 80,
    color: `hsl(${Math.random() * 360}, 70%, 85%)`
  });

  return cardId;
}

// 
function extractConnectionLabel(connectionStr: string): string | null {
  const match = connectionStr.match(/\|([^|]*)\|/);
  return match ? match[1].replace(/<br\s*\/?>/g, '\n') : null;
}


