import { ICard } from '../../types/CoreTypes';
import { AIService } from './aiService';
import { AIOperationResult, AIRole, AIOutputStyle } from '../../types/AITypes';
import { getCardsInViewport, ViewportInfo } from './viewportUtils';

/**
 * 
 * AI
 */
export class DraftExportService {
  constructor(private aiService: AIService) {}

  /**
   * 
   * @param cards 
   * @param viewportInfo 
   * @param customDescription 
   * @param temperature 
   * @param role AI
   * @param outputStyle 
   * @returns 
   */
  async exportDraftFromViewport(
    cards: ICard[],
    viewportInfo: ViewportInfo,
    customDescription?: string,
    temperature?: number,
    role?: AIRole,
    outputStyle?: AIOutputStyle
  ): Promise<AIOperationResult> {
    try {
      // 
      const cardsInViewport = getCardsInViewport(cards, viewportInfo);

      if (cardsInViewport.length === 0) {
        return {
          success: false,
          error: ''
        };
      }

      // AI
      const prompt = this.generateDraftPrompt(
        cardsInViewport.map(card => card.content),
        customDescription
      );

      // AI
      const aiResponse = await this.aiService.sendRequest({
        prompt,
        systemPrompt: this.getDraftSystemPrompt(),
        maxTokens: 4000, // 
        temperature: temperature || 0.7, // 
        role, // 
        outputStyle // 
      });

      if (!aiResponse.success || !aiResponse.content) {
        return {
          success: false,
          error: aiResponse.error || 'AI'
        };
      }

      return {
        success: true,
        data: {
          draftContent: aiResponse.content
        }
      };

    } catch (error) {
      console.error(':', error);
      return {
        success: false,
        error: error instanceof Error ? error.message : ''
      };
    }
  }

  /**
   * 
   */
  private generateDraftPrompt(
    cardContents: string[],
    customDescription?: string
  ): string {
    const contents = cardContents.join('\n');

    // 
    const baseDescription = customDescription || '';

    const prompt = `${baseDescription}


${contents}


1. 
2. 
3. 
4. 
5. 
6. 
7. Markdown

`;

    return prompt;
  }

  /**
   * 
   */
  private getDraftSystemPrompt(): string {
    return `
1. 
2. 
3. 
4. Markdown
5. 
6. `;
  }
}
