/**
 * AI
 * AI
 */

import { AIConfig, AIRequest, AIResponse } from '../../types/AITypes';

export class AIService {
  private config: AIConfig;

  constructor(config: AIConfig) {
    this.config = config;
  }

  /**
   * AI
   */
  updateConfig(config: Partial<AIConfig>): void {
    this.config = { ...this.config, ...config };
  }

  /**
   * 
   */
  getConfig(): AIConfig {
    return { ...this.config };
  }

  /**
   * 
   */
  validateConfig(): boolean {
    return !!(this.config.apiKey && this.config.provider);
  }

  /**
   * 
   */
  private buildEnhancedSystemPrompt(request: AIRequest): string {
    let systemPrompt = request.systemPrompt || '';

    // 
    if (request.role) {
      const rolePrompt = `${request.role.name}
${request.role.description}
${request.role.personality}
${request.role.expertise.join('')}

`;

      systemPrompt = rolePrompt + (systemPrompt ? '\n\n' + systemPrompt : '');
    }

    // 
    if (request.outputStyle) {
      const stylePrompt = `
- ${this.getStyleDescription('tone', request.outputStyle.tone)}
- ${this.getStyleDescription('length', request.outputStyle.length)}

`;

      systemPrompt = systemPrompt + (systemPrompt ? '\n\n' + stylePrompt : stylePrompt);
    }

    return systemPrompt;
  }

  /**
   * 
   */
  private getStyleDescription(type: string, value: string): string {
    const descriptions: Record<string, Record<string, string>> = {
      tone: {
        formal: '',
        casual: '',
        academic: '',
        creative: ''
      },
      length: {
        concise: '',
        detailed: '',
        comprehensive: ''
      }
    };

    return descriptions[type]?.[value] || value;
  }

  /**
   * AI
   */
  async sendRequest(request: AIRequest): Promise<AIResponse> {
    if (!this.validateConfig()) {
      return {
        success: false,
        error: 'AIAPI'
      };
    }

    try {
      // 
      const enhancedRequest = {
        ...request,
        systemPrompt: this.buildEnhancedSystemPrompt(request)
      };

      switch (this.config.provider) {
        case 'openai':
          return await this.sendOpenAIRequest(enhancedRequest);
        case 'deepseek':
          return await this.sendDeepSeekRequest(enhancedRequest);
        case 'anthropic':
          return await this.sendAnthropicRequest(enhancedRequest);
        case 'custom':
          return await this.sendCustomRequest(enhancedRequest);
        default:
          return {
            success: false,
            error: `AI: ${this.config.provider}`
          };
      }
    } catch (error) {
      console.error('AI:', error);
      return {
        success: false,
        error: error instanceof Error ? error.message : ''
      };
    }
  }

  /**
   * OpenAI API
   */
  private async sendOpenAIRequest(request: AIRequest): Promise<AIResponse> {
    const baseUrl = this.config.baseUrl || 'https://api.openai.com/v1';
    const model = this.config.model || 'gpt-3.5-turbo';

    const messages = [];
    if (request.systemPrompt) {
      messages.push({ role: 'system', content: request.systemPrompt });
    }
    messages.push({ role: 'user', content: request.prompt });

    const response = await fetch(`${baseUrl}/chat/completions`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.config.apiKey}`
      },
      body: JSON.stringify({
        model,
        messages,
        max_tokens: request.maxTokens || 2000,
        temperature: request.temperature || 0.7
      })
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(`OpenAI API: ${response.status} - ${errorData.error?.message || response.statusText}`);
    }

    const data = await response.json();
    return {
      success: true,
      content: data.choices[0]?.message?.content || '',
      usage: {
        promptTokens: data.usage?.prompt_tokens || 0,
        completionTokens: data.usage?.completion_tokens || 0,
        totalTokens: data.usage?.total_tokens || 0
      }
    };
  }

  /**
   * DeepSeek API
   */
  private async sendDeepSeekRequest(request: AIRequest): Promise<AIResponse> {
    const baseUrl = this.config.baseUrl || 'https://api.deepseek.com/v1';
    const model = this.config.model || 'deepseek-chat';

    const messages = [];
    if (request.systemPrompt) {
      messages.push({ role: 'system', content: request.systemPrompt });
    }
    messages.push({ role: 'user', content: request.prompt });

    const response = await fetch(`${baseUrl}/chat/completions`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.config.apiKey}`
      },
      body: JSON.stringify({
        model,
        messages,
        max_tokens: request.maxTokens || 2000,
        temperature: request.temperature || 0.7
      })
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(`DeepSeek API: ${response.status} - ${errorData.error?.message || response.statusText}`);
    }

    const data = await response.json();
    return {
      success: true,
      content: data.choices[0]?.message?.content || '',
      usage: {
        promptTokens: data.usage?.prompt_tokens || 0,
        completionTokens: data.usage?.completion_tokens || 0,
        totalTokens: data.usage?.total_tokens || 0
      }
    };
  }

  /**
   * Anthropic API
   */
  private async sendAnthropicRequest(request: AIRequest): Promise<AIResponse> {
    const baseUrl = this.config.baseUrl || 'https://api.anthropic.com/v1';
    const model = this.config.model || 'claude-3-sonnet-20240229';

    const response = await fetch(`${baseUrl}/messages`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'x-api-key': this.config.apiKey,
        'anthropic-version': '2023-06-01'
      },
      body: JSON.stringify({
        model,
        max_tokens: request.maxTokens || 2000,
        system: request.systemPrompt || '',
        messages: [{ role: 'user', content: request.prompt }]
      })
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new Error(`Anthropic API: ${response.status} - ${errorData.error?.message || response.statusText}`);
    }

    const data = await response.json();
    return {
      success: true,
      content: data.content[0]?.text || '',
      usage: {
        promptTokens: data.usage?.input_tokens || 0,
        completionTokens: data.usage?.output_tokens || 0,
        totalTokens: (data.usage?.input_tokens || 0) + (data.usage?.output_tokens || 0)
      }
    };
  }

  /**
   * API
   */
  private async sendCustomRequest(request: AIRequest): Promise<AIResponse> {
    if (!this.config.baseUrl) {
      throw new Error('APIbaseUrl');
    }

    // OpenAIAPI
    const messages = [];
    if (request.systemPrompt) {
      messages.push({ role: 'system', content: request.systemPrompt });
    }
    messages.push({ role: 'user', content: request.prompt });

    // URLbaseUrl
    let apiUrl = this.config.baseUrl;
    if (!apiUrl.includes('/chat/completions') && !apiUrl.includes('/completions')) {
      // URLchat/completions
      apiUrl = apiUrl.endsWith('/') ? apiUrl + 'chat/completions' : apiUrl + '/chat/completions';
    }

    const requestBody = {
      model: this.config.model || 'gpt-3.5-turbo',
      messages,
      max_tokens: request.maxTokens || 2000,
      temperature: request.temperature || 0.7
    };

    console.log('API:', {
      url: apiUrl,
      body: requestBody
    });

    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${this.config.apiKey}`
      },
      body: JSON.stringify(requestBody)
    });

    if (!response.ok) {
      const errorText = await response.text().catch(() => '');
      console.error('API:', {
        status: response.status,
        statusText: response.statusText,
        body: errorText
      });
      throw new Error(`API: ${response.status} - ${response.statusText}${errorText ? '\n' + errorText : ''}`);
    }

    const data = await response.json();
    console.log('API:', data);

    // 
    let content = '';
    if (data.choices && data.choices[0]) {
      // OpenAI
      content = data.choices[0].message?.content || data.choices[0].text || '';
    } else if (data.content) {
      // content
      content = data.content;
    } else if (data.response) {
      // response
      content = data.response;
    } else if (data.output) {
      // output
      content = data.output;
    } else if (typeof data === 'string') {
      // 
      content = data;
    }

    return {
      success: true,
      content,
      usage: data.usage || undefined
    };
  }
}

// AI
let defaultAIService: AIService | null = null;

/**
 * AI
 */
export function getDefaultAIService(): AIService | null {
  return defaultAIService;
}

/**
 * AI
 */
export function setDefaultAIService(config: AIConfig): AIService {
  defaultAIService = new AIService(config);
  return defaultAIService;
}

/**
 * AI
 */
export function clearDefaultAIService(): void {
  defaultAIService = null;
}
