import { ArrowType } from '../../types/CoreTypes';

/**
 * 
 */
export function getArrowTypeName(arrowType?: ArrowType): string {
  switch(arrowType) {
    case ArrowType.NONE: return '';
    case ArrowType.START: return '';
    case ArrowType.END: return '';
    case ArrowType.BOTH: return '';
    default: return '';
  }
}
