import { RefObject } from 'react';

// 
export interface IPosition {
  x: number;
  y: number;
}

export interface ISize {
  width: number;
  height: number;
}

export interface ICard {
  id: string;
  content: string;
  x: number;
  y: number;
  width: number;
  height: number;
  color: string;
}

export enum ArrowType {
  NONE = 'none',
  END = 'end',
  START = 'start',
  BOTH = 'both'
}

// 
export interface IConnection {
  id: string;
  startCardId: string;
  endCardId: string;
  label?: string;
  arrowType?: ArrowType; // 
}

export interface IKeyBindings {
  newCard: string;
  editCard: string;
  deleteCards: string;
  startConnection: string;
  nextCard: string;
  prevCard: string;
  moveUp: string;
  moveDown: string;
  moveLeft: string;
  moveRight: string;
  zoomIn: string;
  zoomOut: string;
  resetView: string;
  save: string;
  load: string;
  showKeyBindings: string;
  copy: string;       // 
  cut: string;        // 
  paste: string;      // 
  undo: string;       // 
  redo: string;       // 
  selectAll: string;  // 
  // 
}

export type CanvasRef = RefObject<HTMLDivElement | null>;

export interface CanvasProps {
  canvasRef: CanvasRef;
  zoomLevel: number;
  pan: { x: number; y: number };
  cards: ICard[];
}

/**
 * 
 * 
 */
export interface MindMapData {
  cards: ICard[];
  connections: IConnection[];
}
